import { useState, useEffect } from 'react';
import { memo } from 'react';
import styles from './LatestNewsForm.module.scss';
import axios from 'axios';
import {
  validateInput,
} from '../../FetchFunctions/fetchFunctionsAdmin/FetchFunctionsAdmin';
import Object from '../../FetchFunctions/fetchFunctionsAdmin/Interfaces';

interface Props {
  token : string;
  change(): void;
  url:string;
  title?:string;
  placeholdOne?:string;
  placeholdTwo?:string;
  placeholdFour?:string;
  removeField1?:boolean,
  removeField2?:boolean,
  removeField3?:boolean;
  removeField4?:boolean,
}


const EditContentForm:React.FC<Props>  = ({
  token,
  change,
  url,
  title,
  placeholdOne,
  placeholdTwo,
  placeholdFour,
  removeField1,
  removeField2,
  removeField3,
  removeField4
}) => {
  const [authorLNF, setauthorLNF] = useState<string>('');
  const [titleLNF, settitleLNF] = useState<string>('');
  const [picLNF, setpicLNF] = useState<string>('');
  const [articleLNF, setarticleLNF] = useState<string>('');
  const [disableSubmit, setdisableSubmit] = useState<boolean>(true);
  const [formMsg, setFormMsg] = useState<string>('');
  const [isPicTooBig,setIsPicTooBig] =  useState<boolean>(false);
  
  const [errorsInputs, seterrorsInputs] = useState<Object>({
    firstField: 'Please Fill Up',
    secondField: 'Please Fill Up',
    thirdField: 'Please Upload Any Image less than 3mb',
    fourthField: 'Please Fill Up'
  });

  const [addNewItem, setAddNewItem] = useState<boolean>(true);

  const openForm = () :void => {
    setAddNewItem(state => !state);
  };

  useEffect(() => {
    setFormMsg('');
   
    if (removeField1) {
      seterrorsInputs(state => ({
        ...state,
        firstField: 'ok'
      }));
    }
    if (removeField2) {
      seterrorsInputs(state => ({
        ...state,
        secondField: 'ok'
      }));
    }
    if (removeField3) {
      seterrorsInputs(state => ({
        ...state,
        thirdField: 'ok'
      }));
    }
    if (removeField4) {
      seterrorsInputs(state => ({
        ...state,
        fourthField: 'ok'
      }));
    }
  }, []);

  useEffect(() => {
    if (
      errorsInputs.firstField === 'ok' &&
      errorsInputs.secondField === 'ok' &&
      // errorsInputs.thirdField === 'ok' &&
      errorsInputs.fourthField === 'ok'
    ) {
      setdisableSubmit(false);
    } else {
      setdisableSubmit(true);
    }
  }, [errorsInputs]);

  const picValidation = (e : any) =>{
    if (e.target.files[0]) {
      if (e.target.files[0].size > 3000000) {
       alert('Image is too big')
       setIsPicTooBig(true)
      }else{
        setIsPicTooBig(false)
      }
    }
   }


  async function sendTitles(e: React.FormEvent<HTMLFormElement>) : Promise<void> {
    e.preventDefault();
   if(!picLNF){
      alert('Please Upload Image');
      return;
    }
   if(isPicTooBig){
    alert('Image is too big');
    return;
   }
   
    const datas = new FormData();
    let check1 = 'ok' ;
    let check2 = 'ok' ;
    let check3 = 'ok' ;
    let check4 = 'ok' ;

    if (removeField1 !== true) {
      datas.append('author', authorLNF);
      check1 = 'Please Fill Up' ;
    }
    if (removeField2 !== true) {
      datas.append('title', titleLNF);
      check2 = 'Please Fill Up' ;
    }
    if (removeField3 !== true) {
      datas.append('img', picLNF);
      check3 = 'Please Upload Any Image less than 3mb' ;
    }
    if (removeField4 !== true) {
      datas.append('article', articleLNF);
      check4 = 'Please Fill Up' ;
    }
    
    const config = {
      headers: { authorization: 'Bearer' + ' ' + token }
    };
    
    

    await axios.post(url, datas, config).then(function(response) {
      if (response.data.status === 'success') {
        change();
      } else {
        alert('There is some problem');
      }
      setFormMsg(response.data.status);
    });
    setdisableSubmit(state => !state);
    setauthorLNF('');
    settitleLNF('');
    setpicLNF('');
    setarticleLNF('');
    setAddNewItem(true);
    seterrorsInputs({
      firstField:  check1,
      secondField: check2,
      thirdField:  check3,
      fourthField: check4
    });
  }

 

  return (
    <div className={styles.LatestNewsForm}>
      <button onClick={openForm} className={styles.addItem}>
        {addNewItem ? 'Add New Item' : 'Close'}
      </button>
      <div
        className={
          addNewItem ? styles.hiddenForm : styles.LatestNewsFormFormwrapper
        }
      >
        <form onSubmit={sendTitles}>
          <div className={styles.LatestNewsFormForm}>
            <h4>{title}</h4>
            {removeField1 !== true ? (
              <>
                <input
                  type="text"
                  placeholder={placeholdOne}
                  name="firstField"
                  value={authorLNF}
                  onChange={e => setauthorLNF(e.target.value)}
                  onKeyUp={e => validateInput(e, 3, 70, seterrorsInputs)}
                />
                {
                  <i
                    className={
                      errorsInputs.firstField === 'ok'
                        ? styles.Ok
                        : styles.notOkNeedVar
                    }
                  >
                    {errorsInputs.firstField}
                  </i>
                }
              </>
            ) : (
              ''
            )}
             {removeField2 !== true ? <><input
              type="text"
              placeholder={placeholdTwo}
              name="secondField"
              value={titleLNF}
              onChange={e => settitleLNF(e.target.value)}
              onKeyUp={e => validateInput(e, 3, 70, seterrorsInputs)}
            />
            <i
              className={
                errorsInputs.secondField === 'Please Fill Up'
                  ? styles.notOkNeedVar
                  : errorsInputs.secondField.includes('Must')
                  ? styles.notOK
                  : styles.Ok
              }
            >
              {errorsInputs.secondField}
            </i></> : ''}
            
            {removeField3 !== true ?<> <input
              type="file"
              name="thirdField"
              className={
                picLNF ? styles.customfileinput : styles.customfileinputempty
              }
              onChange={(e:any) => {setpicLNF(e.target.files[0]) ; picValidation(e)}}
           
            />

            <i
              className={
                errorsInputs.thirdField === 'Please Upload Any Image less than 3mb'
                  ? styles.notOkNeedVar
                  : errorsInputs.thirdField.includes('Must')
                  ? styles.notOK
                  : styles.Ok
              }
            >
              {errorsInputs.thirdField}
            </i></> : ''}
           
            {removeField4 !== true ? (
              <>
                {' '}
                <textarea
                  className={styles.textAreaAd}
                  placeholder={placeholdFour}
                  value={articleLNF}
                  name="fourthField"
                  onChange={e => setarticleLNF(e.target.value)}
                  onKeyUp={e => validateInput(e, 10, 10000, seterrorsInputs)}
                />
                <i
                  className={
                    errorsInputs.fourthField === 'Please Fill Up'
                      ? styles.notOkNeedVar
                      : errorsInputs.fourthField.includes('Must')
                      ? styles.notOK
                      : styles.Ok
                  }
                >
                  {errorsInputs.fourthField}
                </i>
              </>
            ) : (
              ''
            )}

            <input
              className={
                disableSubmit
                  ? styles.LatestNewsFormFormsubmitdisabaled
                  : styles.LatestNewsFormFormsubmit
              }
              disabled={disableSubmit}
              type="submit"
              value="Send"
              name="title"
            />
          </div>
        </form>
      </div>
      <p className="fromMsg">{formMsg}</p>
    </div>
  );
};

export default memo(EditContentForm);
