import { useEffect, useState } from 'react';
import axios from 'axios';
import { validateInput } from '../../../../FetchFunctions/fetchFunctionsAdmin/FetchFunctionsAdmin';
import styles from './editComponent.module.scss';
import style from '../../LatestNewsForm.module.scss';
import { memo } from 'react';
import Object from '../../../../FetchFunctions/fetchFunctionsAdmin/Interfaces';

interface Props {
  targetURL : string ;
  openModal : boolean ;
  geta (): void;
  targetinf : any;
  url :string;
  token:string;
  removeField1?:boolean;
  removeField2?:boolean;
  removeField3?:boolean;
  removeField4?:boolean;
}

const EditComponent:React.FC<Props> = ({
  targetURL,
  openModal,
  geta,
  targetinf,
  url,
  token,
  removeField1,
  removeField2,
  removeField3,
  removeField4
}) => {
  const [titleva, settitle] = useState<string>('');
  const [picva, setpicva] = useState<any>('');
  const [authorva, setauthorva] = useState<string>('');
  const [articleva, setarticle] = useState<string>('');
  const [disableSubmit,setDisableSubmit] = useState<boolean>(false);

  const [errorsInputs, seterrorsInputs] = useState<Object>({
    firstField:  'ok',
    secondField: 'ok',
    thirdField : 'ok',
    fourthField: 'ok'
  });

  useEffect(() : void => {
    setarticle(targetinf.article);
    setauthorva(targetinf.author);
    settitle(targetinf.title);
  }, [targetinf]);


  useEffect(() => {
    if (
      errorsInputs.firstField === 'ok' &&
      errorsInputs.secondField === 'ok' &&
      errorsInputs.thirdField === 'ok' &&
      errorsInputs.fourthField === 'ok'
    ) {
      setDisableSubmit(false);
    } else {
      setDisableSubmit(true);
    }
  }, [errorsInputs]);



  async function submitChanges(e:any) : Promise<void> {
    e.preventDefault();
    const datas = new FormData();
    if (removeField1 !== true) {
      datas.append('author', authorva);
    }
    if (removeField2 !== true) {
      datas.append('title', titleva);
    }
    if (removeField3 !== true) {
      datas.append('img', picva);
    }
    if (removeField4 !== true) {
      datas.append('article', articleva);
    }
    const config = {
      headers: { authorization: 'Bearer' + ' ' + token }
    };
    await axios
      .patch(`${url}/${targetURL}`, datas, config)
      .then(function(response) {
        if (response.data.status === 'success') {
          geta();
        } else {
          alert('There is some problem');
        }
      });
    setTimeout(() => {
      settitle('');
      setpicva('');
      setauthorva('');
      setarticle('');
    }, 500);
  }
  return (
    <div
      className={
        openModal
          ? styles.charactersupdatemodalwrapper2
          : styles.charactersupdatemodalwrapperclose
      }
    >
      <p>Update Character</p>
      <form onSubmit={submitChanges}>
        <div className={styles.charactersupdatemodal}>
          {removeField1 !== true ? (
            <>
              <input
                type="text"
                name="firstField"
                value={authorva || ''}
                onChange={e => setauthorva(e.target.value)}
                onBlur={e => validateInput(e, 3, 70, seterrorsInputs)}
              />{' '}
              {
                <i
                  className={
                    errorsInputs.firstField === 'Please Fill Up'
                      ? styles.notOkNeedVar
                      : errorsInputs.firstField.includes('Must')
                      ? styles.notOK
                      : styles.Ok
                  }
                >
                  {errorsInputs.firstField}
                </i>
              }
            </>
          ) : (
            ''
          )}
            
          {removeField2 !== true ? <> <input
            type="text"
            name="secondField"
            value={titleva || ''}
            onChange={e => settitle(e.target.value)}
            onBlur={e => validateInput(e, 3, 70, seterrorsInputs)}
          />
          <i
            className={
              errorsInputs.secondField === 'Please Fill Up'
                ? styles.notOkNeedVar
                : errorsInputs.secondField.includes('Must')
                ? styles.notOK
                : styles.Ok
            }
          >
            {errorsInputs.secondField}
          </i></> : ''}
          {removeField3 !== true ? <><input
            type="file"
            name="Pic"
            className={
              picva ? style.customfileinput : style.customfileinputempty
            }
            onChange={(e:any) => setpicva(e.target.files[0])}
          />
          <i className={styles.notOkNeedVar}>Optional (Less than 2 mb)</i></> : ''}



          {removeField4 !== true ? (
            <>
              <textarea
                name="fourthField"
                value={articleva || ''}
                onChange={e => setarticle(e.target.value)}
                onBlur={e => validateInput(e, 3, 10000, seterrorsInputs)}
              />
              <i
                className={
                  errorsInputs.fourthField === 'Please Fill Up'
                    ? styles.notOkNeedVar
                    : errorsInputs.fourthField.includes('Must')
                    ? styles.notOK
                    : styles.Ok
                }
              >
                {errorsInputs.fourthField}
              </i>
            </>
          ) : (
            ''
          )}
          <div className={styles.modalButtons}>
            {' '}
            <input
              className={styles.submitModal}
              type="submit"
              value="SAVE"
              disabled={disableSubmit}
            />{' '}
            <button
              className={styles.closeModal}
              name="data"
              type="button"
              onClick={() => geta()}
            >
              CLOSE
            </button>
          </div>
        </div>
      </form>
    </div>
  );
};

export default memo(EditComponent);
