import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogTitle from '@mui/material/DialogTitle';


interface Props {
  openPrompt : boolean;
  el : any ;
  handleClosePrompt() :void;
  deletetitle(el :any) : Promise<void>;
}

const ModalForDelete:React.FC<Props> = ({openPrompt,el,handleClosePrompt,deletetitle,}) => {
 
  
  return (
    <div>
      <Dialog
        open={openPrompt}
        onClose={handleClosePrompt}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          "Are you sure you want to delete this item ?"
        </DialogTitle>
        <DialogActions>
          <Button onClick={handleClosePrompt}>Disagree</Button>
          <Button onClick={() =>deletetitle(el)} autoFocus>Agree</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};
export default ModalForDelete;
