import { useCallback, useEffect, useState } from 'react';
import { memo } from 'react';
import { getData,updateTitlesData,deleteTitlesData} from '../../../FetchFunctions/fetchFunctionsAdmin/FetchFunctionsAdmin';
import styles from './latestNewsDisplay.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilePen } from '@fortawesome/free-solid-svg-icons';
import { faTrash } from '@fortawesome/free-solid-svg-icons';
import EditComponent from './editComponent/EditComponent';
import domain from '../../../../config';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
// import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import ModalForDelete from './modalForDelete/modalForDelete';

interface Props {
  token : string ;
  url :string ;
  comunication : boolean ;
  removeField1? : boolean ;
  removeField2? : boolean ;
  removeField3? : boolean ;
  removeField4? : boolean ;
}

interface Data {
  author: string ;
  img :string ;
  time :string ;
  title :string ;
  id :string;
}

const LatestNewsDisplay:React.FC<Props> = ({
  token,
  url,
  comunication,
  removeField1,
  removeField2,
  removeField3,
  removeField4
}) => {
  const [dataLNF, setdataLNF] = useState<Data[]>([]);
  const [seatchLNF, setseatchLNF] = useState<any>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  // const [isDragAndDropUpdated, setIsDragAndDropUpdated] = useState<boolean>(false);
  const [openPrompt, setOpenPrompt] = useState<boolean>(false);
  // const [enableDnd,setEnableDnd] = useState<boolean>(false);
  const handleClosePrompt =  () : void => {
    setOpenPrompt(false);
  };

 ///////// Drag And Drop Deactivated For Now



  // const handleDrop = (droppedItem :any) :void => {
  //   // Ignore drop outside droppable container
  //   if (!droppedItem.destination) return;
  //   let updatedList = [...dataLNF];
  //   // Remove dragged item
  //   const [reorderedItem] = updatedList.splice(droppedItem.source.index, 1);
  //   // Add dropped item
  //   updatedList.splice(droppedItem.destination.index, 0, reorderedItem);
  //   // Update State
  //   setdataLNF(updatedList);
   
  // };

  // const alertDnd =() : void =>{
  //   alert('Drag and Drop is not enabled')
  // }

  
  // const updateDragDrop = async () : Promise<void> => {
  //   if(!enableDnd){
  //     alertDnd();
  //     return ;
  //   }
  //   if (!isDragAndDropUpdated){
  //   await  updateTitlesData(dataLNF,'/dragdrop' ,url, token);
  //   setIsDragAndDropUpdated(true);
  //   setEnableDnd(false);
  //   setTimeout(() => {
  //     setIsDragAndDropUpdated(false);
  //   }, 3000);
  // }
  
  // };

  const reload = useCallback(() : void => {
    setTimeout(()=>{getData(url).then(Data => setdataLNF(Data));},100)
    setseatchLNF('');
  },[]);

  useEffect(() :void => {
    reload();
  }, [comunication]);

  useEffect(() : void => {
    setIsLoading(true);
    getData(url).then(Data => setdataLNF(Data));
    setIsLoading(false);
  }, []);

  async function deletetitle(el :any) : Promise<void>  {
      await deleteTitlesData(el.id, url, token, {img : el.img});
      handleClosePrompt();
      reload();  
  }
  //////////Update
  const [thisurl, setthisurl] = useState<string>('wrong');
  const [openModal, setopenModal] = useState<boolean>(false);
  const [newsModalinf, setnewsModalinf] = useState<string>('');

  const saveinformation = useCallback((char : string ) :void => {
    setnewsModalinf(char);
  },[])

  const saveUrl= useCallback((el : string) =>{
    setthisurl(el);
  },[]);
  
  const saveState = useCallback(() : void => {
    setopenModal(state => !state);
    reload();
  },[]);


  const saveAll = useCallback((el :any, char :any) => {
    saveUrl(el);
    saveinformation(char);
    saveState();
  },[])

  function search() : void {
    if (seatchLNF.length === 0) {
      reload();
    }
    setdataLNF(
      dataLNF.filter((el:any)  =>
        el.title.toLowerCase().includes(seatchLNF.toLocaleLowerCase())
      )
    );
  }

  useEffect(() => {
    search();
  }, [seatchLNF]);

  try {
    return (
      <div>
        <div className={styles.searchdisplay}>
          <h2>Search</h2>
          <div>
            <input
              type="text"
              value={seatchLNF}
              onChange={e => setseatchLNF(e.target.value)}
            />
            <button type="button" onClick={reload}>
              Reset
            </button>
          </div>
        </div>
        {/* <button 
                className={enableDnd ? styles.dragDropUpdatePermission   : styles.dragDropUpdated} 
                onClick={()=>setEnableDnd(true)}>{enableDnd ? 'Enabled' : 'Enable Drag and Drop'}
                </button>
        <div>      
          <button
            className={
              !isDragAndDropUpdated
                ? styles.dragDropUpdate
                : styles.dragDropUpdated
            }
            onClick={updateDragDrop}
          >
            {!isDragAndDropUpdated ? 'Drag and Drop Update' : 'Updated'}
          </button>
        </div> */}
        {!isLoading ? (
          <div className={styles.dragDrop}>
            {/* <DragDropContext onDragEnd={enableDnd ? handleDrop : alertDnd}>
              <Droppable droppableId="list-container">
                {provided => (
                  <div
                    className="list-container"
                    {...provided.droppableProps}
                    ref={provided.innerRef}
                  > */}
                    {dataLNF?.map((el :any , index : number) => (
                      // <Draggable
                      //   key={el.id}
                      //   draggableId={el.img}
                      //   index={index}
                      // >
                      //   {provided => (
                      //     <div
                      //      className={styles.wrapperTr}
                      //       ref={provided.innerRef}
                      //       {...provided.dragHandleProps}
                      //       {...provided.draggableProps}
                      //     >
                      //       {
                      
                              <div key={el.id} className={styles.titelsDisplay}>
                                
                                <div className={styles.titlesinform}>
                                  <table>
                                    <tbody>
                                      <tr className={styles.titlesinformTr}>
                                        <td>
                                          <LazyLoadImage
                                            effect="blur"
                                            src={`${domain}/${el.img}`}
                                          />
                                        </td>
                                        <td>
                                          <div
                                            className={styles.titlesinformTrDiv}
                                          >
                                            <p>{el.title}</p>
                                            {el.author &&
                                            el.author.length < 25 ? (
                                              <p>{el.author}</p>
                                            ) : (
                                              <p />
                                            )}
                                            <p> Create : {el.time}</p>
                                          </div>{' '}
                                        </td>
                                        <td
                                          className={
                                            styles.titlesinformTrfontawesome
                                          }
                                        >
                                          <FontAwesomeIcon
                                            onClick={() => saveAll(el.id, el)}
                                            className={styles.fontawesomeLND}
                                            icon={faFilePen}
                                          />
                                        </td>
                                        <td
                                          className={
                                            styles.titlesinformTrfontawesome
                                          }
                                        >
                                          <FontAwesomeIcon
                                            onClick={() => setOpenPrompt(true)}
                                            className={styles.fontawesomeLND}
                                            icon={faTrash}
                                          />
                                        </td>          
                                      </tr>
                                    </tbody>
                                  </table>
                                  <ModalForDelete openPrompt={openPrompt} handleClosePrompt={handleClosePrompt} deletetitle={deletetitle} el={el} />
                                </div>
                              </div>
                      //       }
                      //     </div>
                      //   )}
                      // </Draggable>
                    ))}
                    {/* {provided.placeholder} */}
                  {/* </div>
                )}
              </Droppable>
            </DragDropContext> */}
          </div>
        ) : (
          <span>Loading Database...</span>
        )}
        <EditComponent
          targetURL={thisurl}
          targetinf={newsModalinf}
          openModal={openModal}
          geta={saveState}
          url={url}
          token={token}
          removeField1={removeField1}
          removeField2={removeField2}
          removeField3={removeField3}
          removeField4={removeField4}
        />
      </div>
    );
  } catch {
    return <p className={styles.errLoading}>Error Loading DB</p>;
  }
};
export default memo(LatestNewsDisplay);
