import EditContentForm from './EditContentForm/EditContentForm';
import LatestNewsDisplay from './EditContentForm/latestNewsDisplay/LatestNewsDisplay';
import styles from './LatestNews.module.scss';
import { useCallback, useState } from 'react';
import { useContext } from 'react';
import { TaskContext } from '../../components/Context/TaskContext';

interface Props {
  url:string,
  title?:string,
  placeholdOne?:string,
  placeholdTwo?:string,
  placeholdFour?:string,
  removeField1?:boolean,
  removeField2?:boolean,
  removeField3?:boolean,
  removeField4?:boolean,
}

const EditContent:React.FC<Props> = ({
  url,
  title,
  placeholdOne,
  placeholdTwo,
  placeholdFour,
  removeField1,
  removeField2,
  removeField3,
  removeField4
}) => {
  const [coomTwochilds, setcoomTwochilds] = useState<boolean>(true);
  const { token } :any = useContext(TaskContext);

  const change  = () : void => {
    setcoomTwochilds(state => !state);
  }


  return (
    <div className={styles.LatestNews}>
      <EditContentForm
        token={token}
        change={change}
        url={url}
        title={title}
        placeholdTwo={placeholdTwo}
        placeholdOne={placeholdOne}
        placeholdFour={placeholdFour}
        removeField1={removeField1}
        removeField2={removeField2}
        removeField3={removeField3}
        removeField4={removeField4}
      />
      <LatestNewsDisplay
        token={token}
        url={url}
        comunication={coomTwochilds}
        removeField1={removeField1}
        removeField2={removeField2}
        removeField3={removeField3}
        removeField4={removeField4}       
      />
    </div>
  );
};

export default EditContent;
